#login-form{position:relative;display:block;width:520px;height:598px;text-align:center;padding:48px 32px;background:#fff;margin:auto}#login-form-title{display:block;padding-bottom:24px;width:100%}#login-form-desc{display:block;padding-bottom:40px}.btn-login{position:relative;display:inline-block;width:320px;height:60px;line-height:58px;border:solid 2px #000;border-radius:30px;margin-bottom:10px;cursor:pointer}.btn-login .icon{position:relative;display:inline-block;vertical-align:top;width:50px;height:40px;margin:9px 10px 9px 0;background-position:0 center;background-repeat:no-repeat}.btn-login span{position:relative;display:inline-block;vertical-align:top}.btn-login:hover .icon,.btn-login:active .icon{background-position:-50px center}#btn-login-facebook{border-color:#425993;color:#425993}#btn-login-facebook .icon{background-image:url("/static/cojam/res/ic-login-facebook.png")}#btn-login-facebook:hover,#btn-login-facebook:active{background-color:#425993;color:#fff}#btn-login-google{border-color:#f34a38;color:#f34a38}#btn-login-google .icon{background-image:url("/static/cojam/res/ic-login-google.png")}#btn-login-google:hover,#btn-login-google:active{background-color:#f34a38;color:#fff}#btn-login-kakao{border-color:#423530;color:#423530}#btn-login-kakao .icon{background-image:url("/static/cojam/res/ic-login-kakao.png")}#btn-login-kakao:hover,#btn-login-kakao:active{background-color:#423530;color:#fff}#btn-login-line{border-color:#27be0f;color:#27be0f}#btn-login-line .icon{background-image:url("/static/cojam/res/ic-login-line.png")}#btn-login-line:hover,#btn-login-line:active{background-color:#27be0f;color:#fff}
